import React from 'react'
import { useSelector } from 'react-redux';

import { Avatar, Typography, Space, Badge, Menu, Dropdown } from 'antd';
import {  BellOutlined, UserOutlined } from '@ant-design/icons';

export const UserOptions = () => {


    const { Text } = Typography;

    const { name, photoURL } = useSelector( state => state.auth );

    const menu = (
        <Menu>
          <Menu.Item>
            <a target="_blank" rel="noopener noreferrer" href="https://www.antgroup.com">
              Leandro Cordero ha cargado un nuevo prestador
            </a>
          </Menu.Item>
          <Menu.Item>
            <a target="_blank" rel="noopener noreferrer" href="https://www.aliyun.com">
            Leandro Cordero ha cargado un nuevo prestador
            </a>
          </Menu.Item>
          <Menu.Item>
            <a target="_blank" rel="noopener noreferrer" href="https://www.luohanacademy.com">
              Leandro Cordero ha cargado un nuevo prestador
            </a>
          </Menu.Item>
        </Menu>
      );

   
    return (
        <div className="navsettings__container">
                    <Space>
                     
                        <Text type="secondary">{ name }</Text>
                       
                           
                         <div>
                         <Dropdown overlay={menu} placement="bottomRight" arrow>
                         <Badge count={5} size={"small"}>
                         { photoURL ?
                         
                     <Avatar shape="square" size={45} src={photoURL} />
                    :
                    <Avatar shape="square" size={45} icon={<UserOutlined />} />
                    
                    }
                        </Badge>
                        </Dropdown>
                         </div>
               
                    </Space>
               
               </div>
 
    )
}
