import React, { useEffect } from 'react';
import {
    BrowserRouter as Router,
    Redirect,
    Switch,
} from "react-router-dom";

import { PrivateRoute } from './PrivateRoute';
import { PublicRoute } from './PublicRoute';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import { useDispatch, useSelector } from 'react-redux';
import { startChecking } from '../actions/auth';
import { LoginScreen } from '../components/auth/LoginScreen';
import { RegisterScreen } from '../components/auth/RegisterScreen';
import { GridPrestadores } from '../components/prestadores/GridPrestadores';
import { FilePrestador } from '../components/prestadores/FilePrestador';
import { DashboardUi } from '../components/ui/DashboardUi';
import { FormPrestador } from '../components/prestadores/FormPrestador';
import { Spin } from 'antd';


export const AppRouter = () => {

    const {  uid, checking } = useSelector( state => state.auth );
   
  
const dispatch = useDispatch();

useEffect(() => {
    dispatch(startChecking())
}, [dispatch])


return (

    
          <Router>
            <>
            <Spin spinning={checking}>
           <Switch>
                    <PublicRoute exact
                                 path="/auth/login"
                                 component={ LoginScreen }
                                 isAuthenticated={ !!uid }
                                 />

                    <PublicRoute exact
                                 path="/auth/register"
                                 component={ RegisterScreen }
                                 isAuthenticated={ !!uid }
                                 />

                    <PrivateRoute exact
                                  path="/dash"
                                  component={ DashboardUi }
                                  isAuthenticated={ !!uid } />
                    
                    <PrivateRoute exact
                                  path="/prestadores"
                                  component={ GridPrestadores }
                                  isAuthenticated={ !!uid } />
                    
                    <PrivateRoute exact
                                  path="/prestadores/consulta"
                                  component={ FilePrestador }
                                  isAuthenticated={ !!uid } />

                     <PrivateRoute exact
                                  path="/prestadores/agregar"
                                  component={ FormPrestador }
                                  isAuthenticated={ !!uid } />

                    <PrivateRoute exact path="/:id" component={ FormPrestador }
                                  isAuthenticated={ !!uid } />
                    
                    <Redirect to="/dash" />
                </Switch>
             
                </Spin>

              
            </>
            </Router>   
      
            )}